import {
	mapState,
	mapMutations,
	mapGetter
} from 'vuex'
export default {
	data() {
		return {
			staticURL: this.$url.staticURL,
			uploadURL: this.$url.uploadURL,
			page: 1,
			key: '',
			list: [1, 2],
			genderIndex: 0,
			gender: ['配菜员', '服务员'],
			moneyIndex: 0,
			money: ['1000-2000', '2000-6000','6000以上'],
		}
	},
	onLoad() {
		const vm = this;
	},
	methods: {
		changeGender(e) {
			this.genderIndex = e.target.value
		},
		changeMoney(e) {
			this.moneyIndex = e.target.value
		},
		// 搜索
		search() {
			this.list = [];
			this.apiSearch();
		},
		// 搜索api
		apiSearch() {
			const vm = this;
			if (!vm.key) {
				vm.$utils.toast('请输入课程名称');
				return false;
			}
			COMMON.show();
			API('goodsList', {
					class_id: '',
					goods_name: self.data.key,
					page: self.data.page
				})
				.then((e) => {
					COMMON.hide();
					if (e.code == 200) {
						if (e.resData.goodsList.data.length == 0) {
							if (self.detail.length <= 0) {
								self.setData({
									detail: []
								})
								COMMON.Toast('未查询到该商品')
							} else {
								COMMON.Toast('已全部加载')
							}
						} else {
							self.setData({
								detail: [...self.detail, ...e.resData.goodsList.data]
							})
							console.log(self.detail)
						}
					}
				})
		},
		goPage(url) {
			this.$utils.goPage(url)
		}
	},
	computed: {
		...mapState(['userInfo'])
	}
}
